@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-weight: 300;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 1.7;
  color: #283645;
  background-color: #ffffff;
}
main {
  flex-grow: 1;
}
img {
  max-width: 100%;
  height: auto;
}
.list-default {
  list-style: none;
  padding: 0;
  margin: 0;
}
.button {
  display: inline-block;
  padding: 16px 20px;
  font-weight: bold;
  font-size: 17px;
  line-height: 1;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
}
.button_circle {
  border: 3px solid #ffffff;
  border-radius: 50px;
}
.button_dark {
  color: #ffffff;
  background-color: #283645;
}
.button_dark:hover,
.button_dark:focus {
  color: #d22856;
  background-color: #ffffff;
}
.button_dark:active {
  color: rgba(210, 40, 86, 0.3);
  background-color: #ffffff;
}
.button_light {
  color: #283645;
  background-color: #ffffff;
}
.button_icon {
  padding: 0;
  margin: 0;
}
.button_nonbk {
  background: none;
}
.button_form {
  font-size: 18px;
  line-height: 1.7;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #2aab6d;
  border: none;
}
.button_form:hover,
.button_form:focus {
  background-color: #1c9b5e;
}
.button_form:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #1c9b5e;
}
.button_send-send,
.button_send-cancel {
  align-self: stretch;
  box-sizing: border-box;
  margin-top: 25px;
  min-width: 185px;
  border-radius: 50px;
  background-color: transparent;
}
.button_send-send {
  color: #d22856;
  border: 3px solid #d22856;
}
.button_send-send:hover,
.button_send-send:focus {
  color: #ffffff;
  background-color: #d22856;
  border: 3px solid #d22856;
}
.button_send-send:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #d22856;
  border: 3px solid #d22856;
}
.button_send-cancel {
  color: rgba(40, 54, 69, 0.1);
  border: 3px solid rgba(40, 54, 69, 0.1);
}
.button_send-cancel:hover,
.button_send-cancel:focus {
  color: rgba(40, 54, 69, 0.3);
  border: 3px solid rgba(40, 54, 69, 0.3);
}
.button_send-cancel:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(40, 54, 69, 0.3);
  border: 3px solid rgba(40, 54, 69, 0.3);
}
@media (max-width: 659.9px) {
  .button_send-send,
  .button_send-cancel {
    margin-top: 15px;
  }
}
.btn-edit {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
}
.btn-edit .btn-edit-svg {
  fill: rgba(0, 0, 0, 0.3);
}
.btn-edit:hover .btn-edit-svg,
.btn-edit:focus .btn-edit-svg {
  fill: rgba(0, 0, 0, 0.5);
}
.paginator {
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: 64px;
  height: 12px;
}
.paginator__point {
  display: inline-block;
  box-sizing: border-box;
  width: 6px;
  height: 6px;
  border-radius: 12px;
  background-color: rgba(126, 134, 143, 0.3);
}
.paginator__point_current {
  width: 12px;
  height: 12px;
  border: 3px solid #283645;
  background-color: #ffffff;
}
.paginator__point:hover,
.paginator__point:focus {
  background-color: #7e868f;
}
.paginator__point:active {
  background-color: rgba(126, 134, 143, 0.1);
}
