.header {
  font-weight: bold;
  font-size: 18px;
  line-height: 1.8;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  background-color: #283645;
}
.header__title {
  position: relative;
  min-height: 67px;
  background-color: #283645;
  border-bottom: #000000 1px solid;
}
.header__menu-link {
  display: block;
  padding: 16px 0;
  color: #ffffff;
  text-decoration: none;
  border-bottom: #000000 1px solid;
  box-shadow: inset 0 1px 0 0 rgba(255, 255, 255, 0.15);
}
.header__menu-link:visited {
  color: #ffffff;
}
.header__logo {
  position: absolute;
  top: 20px;
  left: 20px;
}
.header__logo-piture {
  width: 75px;
  height: 23px;
  fill: #ffffff;
}
.header__logo:hover .header__logo-piture,
.header__logo:focus .header__logo-piture {
  fill: #d22856;
}
.header__logo:active .header__logo-piture {
  fill: rgba(255, 255, 255, 0.3);
}
.header__menu-list a[href]:hover,
.header__menu-list a[href]:focus {
  color: #d22856;
}
.header__menu-list a[href]:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (max-width: 1199.9px) {
  .header__button-menu {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 23px;
    height: 23px;
    cursor: pointer;
    border: none;
    background-color: #283645;
  }
  .header__container_closed .header__button-menu {
    width: 50px;
  }
  .header__container_closed .header__button-menu::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 3px;
    background-color: #ffffff;
    box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
  }
  .header__container_closed .header__button-menu:hover::before,
  .header__container_closed .header__button-menu:focus::before {
    background-color: #d22856;
    box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  }
  .header__container_closed .header__button-menu:active::before {
    background-color: rgba(210, 40, 86, 0.3);
    box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
  }
  .header__container_closed .header__menu-list {
    display: none;
  }
  .header__container_opened .header__button-menu::before,
  .header__container_opened .header__button-menu::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 0;
    width: 23px;
    height: 3px;
    background-color: #ffffff;
  }
  .header__container_opened .header__button-menu::before {
    transform: rotate(45deg);
  }
  .header__container_opened .header__button-menu::after {
    transform: rotate(-45deg);
  }
  .header__container_opened .header__button-menu:hover::before,
  .header__container_opened .header__button-menu:focus::before,
  .header__container_opened .header__button-menu:hover::after,
  .header__container_opened .header__button-menu:focus::after {
    background-color: #d22856;
  }
  .header__container_opened .header__button-menu:active::before,
  .header__container_opened .header__button-menu:active::after {
    background-color: rgba(210, 40, 86, 0.3);
  }
  .header__container_nojs .header__menu-list {
    display: unset;
  }
  .header__container_nojs .header__button-menu {
    display: none;
  }
}
@media (min-width: 660px) {
  .header__title {
    min-height: 110px;
  }
  .header__logo {
    top: 27px;
  }
  .header__logo-piture {
    width: 150px;
    height: 40px;
  }
  .header__button-menu {
    top: 47px;
  }
}
@media (min-width: 1200px) {
  .header {
    position: absolute;
    width: 100%;
    font-weight: normal;
    font-size: 18px;
    line-height: 1;
    text-transform: none;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .header__container {
    display: flex;
    justify-content: space-between;
    max-width: 940px;
    margin: 0 auto;
  }
  .header__title {
    position: unset;
    text-align: left;
    border: none;
    background: none;
  }
  .header__logo {
    position: unset;
    margin-right: 170px;
  }
  .header__logo-piture {
    margin-top: 27px;
  }
  .header__button-menu {
    display: none;
  }
  .header__menu-list {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
  }
  .header__menu-link {
    display: block;
    padding: 45px 0;
    padding-bottom: 10px;
    border: none;
    box-shadow: none;
  }
  .header__menu-link_current {
    border-bottom: 3px solid #ffffff;
  }
  .header__menu-list a[href]:hover,
  .header__menu-list a[href]:focus {
    color: #d22856;
  }
  .header__menu-list a[href]:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
